import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import UIkit from "uikit";
import "uikit/dist/css/uikit.min.css";
import Icons from "uikit/dist/js/uikit-icons";
import { format, fromUnixTime } from "date-fns";
import "babel-polyfill";

// loads the Icon plugin
UIkit.use(Icons);
window.UIkit = UIkit;

const ArticlesList = () => {
  const [data, setData] = useState({ items: [], count: 9 });
  const fetchData = async () => {
    const feed = await fetch(
      "https://script.google.com/macros/s/AKfycbw0UXHWCNDhYDJa_tGyFNM7pVROJ72LZs2O3QTNO1WLoBYeNQI27rEcfYS0TK-q9rU/exec"
    );
    const items = await feed.json()
    setData({ items: items, count: 12 });
  };

  const more = () => {
    setData({ items: data.items, count: data.count + 6 });
  };

  useEffect(() => {
    fetchData();
  }, []);

  return (
    <div>
      <div className="uk-child-width-1-3@m uk-grid uk-grid-match">
        {data.items.slice(0, data.count).map(item => (
          <div className="uk-margin-bottom" key={item.url}>
            <a
              href={item.url}
              target="_blank"
              className="uk-card uk-card-default uk-card-small uk-card-hover uk-link-reset"
            >
              <div className="uk-card-media-top">
                <img
                  style={{
                    objectFit: "cover",
                    backgroundColor: "gray",
                    height: "150px",
                    width: "100%",
                  }}
                  src={item.image}
                />
              </div>
              <div className="uk-card-body">
                {format(fromUnixTime(parseInt(item.date)), "yyyy-MM-dd") + " " + item.title}
              </div>
            </a>
          </div>
        ))}
      </div>
      <div onClick={more} className="uk-button uk-button-default uk-width-1-1">
        MORE
      </div>
    </div>
  );
};

ReactDOM.render(<ArticlesList />, document.getElementById("list"));
